// App.jsx
import React from 'react';
import { Route, Switch } from 'react-router-dom';
import AlanComponent from './AlanComponent'; // Import the AlanComponent here
import Help from './Help';
import HomePage from './HomePage';
import PlayerInfoPage from './PlayerInfoPage';
import PageNotFound from './PageNotFound';

const App = () => {
  return (
    <>
      <AlanComponent /> {/* Include the Alan AI component here */}
      <Switch>
        <Route exact path="/" component={HomePage} />
        <Route exact path="/playerinfo/:name" component={PlayerInfoPage} />
        <Route exact path="/help" component={Help} />
        <Route path="*" component={PageNotFound} />
      </Switch>
    </>
  );
};

export default App;
