import React from 'react';
import {Link} from 'react-router-dom';
import RadarChart from './RadarChart'
import {FaHome, FaQuestionCircle} from 'react-icons/fa'
import PlayerAttributes from './SpecificPlayerAttributes'
import Melbourne from './resources/images/melbourne-modified.png'
import Messi from './resources/images/Messi.png'
import Neymar from './resources/images/Neymar.png'
import DeGea from './resources/images/DeGea.png'
import Bruyne from './resources/images/Bruyne.png'



const PlayerFound = ({playerObject}) => {

    const iconStyling = {
        height: '30', 
        width: '30',  
        marginRight: '5px', 
        display: 'inline'
    };

    let imgSrc;

    if(playerObject.Ground === 'Melboure Cricket Ground (MCG)')
        imgSrc = Melbourne;
    else if(playerObject.Ground === 'Sydney Cricket Ground (SCG)')
        imgSrc = Messi;
    else if(playerObject.Ground === 'Adelaide Oval')
        imgSrc = Neymar;
    else if(playerObject.Ground === 'The Gabba')
        imgSrc = DeGea;
    else
        imgSrc = Bruyne;

    return(
        <>
            <div className="player-stats-container-div">
                <div className="navbar">
                    <Link to="/"><FaHome style={iconStyling}/><p id="home-button">Home</p></Link>
                    <Link to="/help"><FaQuestionCircle style={iconStyling}/><p id="help-button">Help</p></Link>
                </div>
                <div className="sub-container">
                    <div className="player-name-container">
                        <p className="player-name">{playerObject.Ground}</p>
                        <img src={imgSrc} alt={playerObject.Ground} height={70} width={70}/>
                    </div>
                    <div className="player-stats-container">
                        <div className="col-left">
                            <RadarChart player={playerObject}/>
                        </div>
                        <div className="col-right">
                            <div className="concise-data-container">
                                
                            </div>
                            <div className="list-container">
                                <PlayerAttributes player={playerObject}/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}

export default PlayerFound;