// AlanComponent.jsx
import React, { useEffect } from 'react';
import alanBtn from '@alan-ai/alan-sdk-web';

const AlanComponent = () => {
  useEffect(() => {
    alanBtn({
      key: '6f3dae70c9ccbb2e4cd40ca566cafd002e956eca572e1d8b807a3e2338fdd0dc/stage', // Replace with your Alan AI API key
      onCommand: (commandData) => {
        if (commandData.command === 'showStadiumDetails') {
          // Get the stadium details from commandData and display them in your React app
          const stadium = commandData.stadium;
          // Add your logic here to display the stadium details in your app
          console.log(stadium);
        }
      },
    });
  }, []);

  return <div style={{ visibility: 'hidden' }}>Alan AI</div>; // Hidden div for Alan AI button
};

export default AlanComponent;
